@import "../../../assets/scss/theme-variable.scss";

.login-section-wrapper {
  height: 100%;
  background-image: linear-gradient(
    to right top,
    #000000,
    #342a2a,
    #65504b,
    #987b6c,
    #c8ac8d,
    #dbbf93,
    #ecd49a,
    #f9eaa1,
    #f4e18b,
    #efd974,
    #ebd05d,
    #e6c744
  );
  .login-form-container {
    background-color: #ffffff;
    border-radius: 20px;
    width: 40%;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (max-width: 850px) {
      width: 50%;
    }

    @media (max-width: 760px) {
      width: 75%;
    }

    .login-form-section {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 20px 30px;

      @media (max-width: 400px) {
        padding: 5px 15px;
      }

      h1 {
        color: black;
        font-size: 25px;
      }

      .signin-btn {
        background-color: black !important;
        width: 150px;
        border-radius: 50px;
        cursor: pointer;
      }

      .signin-divider {
        padding: 20px;
        width: 100%;
        color: rgb(88, 88, 88);
      }

      .social-login-btn-wrapper {
        display: flex;
        align-items: center;
      }

      .account-wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 100% !important;
        padding-top: 20px;

        @media (max-width: 950px) {
          font-size: 12px;
        }
        .forgot-pass {
          color: black;
          padding-bottom: 5px;
          align-items: flex-start !important;
          margin: 0;
          cursor: pointer;
          text-decoration: underline;
        }
        .create-account {
          display: flex;
          color: rgb(88, 88, 88);
          p {
            color: black;
            margin-left: 10px !important;
            text-decoration: underline;
            margin: 0;
            cursor: pointer;
          }
        }
      }
    }
  }
}
